const App = () => {
    //  
    const [currentQuestion, setCurrentQuestion] = useState(0);
    const [selectedAnswers, setSelectedAnswers] = useState(Array(20).fill(null)); // 20    
    const [showResult, setShowResult] = useState(false);

    //        App 
    const handlePrevious = () => {
        if (currentQuestion > 0) {
            setCurrentQuestion(currentQuestion - 1);
        }
    };

    const props = {
        currentQuestion,
        setCurrentQuestion,
        showResult,
        setShowResult,
        handlePrevious,
        questions,
        selectedAnswers,
        setSelectedAnswers,
    };

    return (
        <UserProvider>
            <BrowserRouter basename={`${process.env.PUBLIC_URL}`}>
                <Routes>
                    <Route element={<Layout />}>
                        <Route path="/" element={<Navigate to="/login" />} />
                        <Route path="/login" element={<Login />} />
                        <Route path="/privacyPolicyNotice" element={<PrivacyPolicyNotice />} />
                        <Route path="/submitsummary" element={<SubmitSummary />} />
                        <Route path="/question" element={<Question {...props} />} />
                        <Route path="/result" element={<Result {...props} />} />
                    </Route>
                </Routes>
            </BrowserRouter>
        </UserProvider>
    );
};

export default App;
